/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Staatliches", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Staatliches", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.hero {
  min-height: 60rem;
  background-image: url(../img/hero.jpg);
  background-size: cover;
  background-position: center;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hero__heading {
  max-width: 60rem;
  color: #FFFFFF;
}
.hero__link {
  background-color: #ff8015;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .hero__link {
    width: auto;
  }
}
.hero__link:hover {
  background-color: #e16700;
}

.search {
  background-color: #F2F2F2;
  padding: 5rem 0;
}
.search__heading {
  color: #484349;
  margin: 0;
  text-align: center;
}

.form {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .form {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.form__campo {
  padding: 1rem;
  border: 1px solid #c7c3c8;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .form__campo {
    margin-bottom: 0;
  }
}
.form__submit {
  background-color: #B8336A;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  margin-top: 0;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
  }
}
.form__submit:hover {
  background-color: #902853;
}

.eventos {
  padding: 5rem 0;
}
.eventos__heading {
  color: #484349;
  margin: 0;
  text-align: center;
}
.eventos__grid {
  margin-top: 2rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .eventos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.evento {
  border: 1px solid #F2F2F2;
}
.evento__container {
  padding: 2rem;
}
.evento__imagen {
  width: 100%;
}
.evento__fecha {
  font-size: 1.8rem;
}
.evento__nombre {
  color: #484349;
}
.evento__auth {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-top: 2rem;
}
.evento__auth-imagen {
  width: 10rem;
  border-radius: 50%;
  overflow: hidden;
}
.evento__organizado-texto, .evento__organizado-nombre {
  margin: 0.5rem 0;
}
.evento__organizado-texto {
  font-weight: 700;
}

.categories {
  background-color: #F2F2F2;
  padding: 5rem 0;
}
.categories__heading {
  color: #484349;
  margin: 0;
  text-align: center;
}
.categories__grid {
  margin-top: 5rem;
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .categories__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .categories__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.categoria__nombre {
  text-align: center;
  padding: 2rem;
}

.grupos {
  color: #FFFFFF;
  padding: 5rem 0;
  background-image: linear-gradient(to bottom, #B8336A 0%, #B8336A 50%, #068D9D 50%, #068D9D 100%);
}
@media (min-width: 768px) {
  .grupos {
    background-image: linear-gradient(to right, #B8336A 0%, #B8336A 50%, #068D9D 50%, #068D9D 100%);
  }
}
.grupos__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .grupos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.grupo__texto {
  color: #FFFFFF;
}

.header {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    gap: 2rem;
  }
}
.nav__link {
  margin-bottom: 2rem;
  font-size: 1.4rem;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .nav__link {
    margin-bottom: 0;
  }
}
.nav__link:last-of-type {
  margin-bottom: 0;
}
.nav__link--crear {
  font-weight: 700;
  color: #B8336A;
}
@media (min-width: 768px) {
  .nav__link--crear::after {
    content: "";
    display: inline-block;
    height: 4rem;
    margin: 0 0 0 2rem;
    border-right: 1px solid #F2F2F2;
  }
}

.footer {
  padding-top: 5rem;
}
.footer__cr {
  text-align: center;
  padding: 1rem;
}